import React from 'react';

import { userService } from '../_services';

class LoginPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            username: '',
            password: '',
            submitted: false,
            loading: false,
            error: ''
        };
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(e) {
        const { name, value } = e.target;
        this.setState({ [name]: value });
    }

    handleSubmit(e) {
        e.preventDefault();
        
        this.setState({ submitted: true });
        const { username, password, returnUrl } = this.state;
        // stop here if form is invalid
        if (!(username && password)) {
            return;
        }

        this.setState({ loading: true });
        userService.login(username, password)
        .then(
            user => {
                this.props.history.push("/");
                this.props.history.go("/");
            },
            error => this.setState({ error, loading: false })
        )
    }

    render() {
        const { username, password, submitted, loading, error } = this.state;
        return (            
            <div className="col-md-6 col-md-offset-3 justify-content-center">
                <h2>Se connecter</h2>
                <form name="form" onSubmit={this.handleSubmit}>
                    <div className={'form-group' + (submitted && !username ? ' has-error' : '')}>
                        <label htmlFor="username">Nom d'utilisateur</label>
                        <input type="text" className="form-control" name="username" placeholder="nom d'utilisateur" value={username} onChange={this.handleChange} />
                        {submitted && !username &&
                            <div className="help-block">Nom d'utilisateur requis</div>
                        }
                    </div>
                    <div className={'form-group' + (submitted && !password ? ' has-error' : '')}>
                        <label htmlFor="password">Mot de passe</label>
                        <input type="password" className="form-control" name="password" placeholder='mot de passe' value={password} onChange={this.handleChange} />
                        {submitted && !password &&
                            <div className="help-block">Mot de passe requis</div>
                        }
                    </div>
                    <div className="form-group">
                        <button className="btn btn-primary" disabled={loading}>Se connecter</button>
                        {loading &&
                            <img src="data:image/gif;base64," />
                        }
                    </div>
                    {error &&
                        <div className={'alert alert-danger'}>{error}</div>
                    }
                </form>
            </div>
        );
    }
}

export { LoginPage }; 